					selected: false
				}, {
					name: "诺基亚5",
					selected: false
				}, {
					name: "荣耀4",
					selected: false
				}, {
					name: "诺基",
					selected: false
				}, {
					name: "荣耀",
					selected: false
				}, {
					name: "诺基亚2",
					selected: false
				}, {
					name: "荣耀2",
					selected: false
				}, {
					name: "诺基",
					selected: false
				}]
			}],
			items: [],
			loadding: false,
			pullUpOn: true
		}
	},
	onLoad: function(options) {
		let obj = {};

		obj = wx.getMenuButtonBoundingClientRect();







		this.searchKey = options.q || "";
		
		uni.getSystemInfo({
			success: (res) => {
				this.width = obj.left || res.windowWidth;
				this.height = obj.top ? (obj.top + obj.height + 8) : (res.statusBarHeight + 44);
				this.inputTop = obj.top ? (obj.top + (obj.height - 30) / 2) : (res.statusBarHeight + 7);
				this.arrowTop = obj.top ? (obj.top + (obj.height - 32) / 2) : (res.statusBarHeight + 6);
				//略小，避免误差带来的影响
				// this.dropScreenH = this.height * 750 / res.windowWidth + 186;
				this.dropScreenH = this.height * 750 / res.windowWidth + 76;
				this.drawerH = res.windowHeight - uni.upx2px(100) - this.height
			}
		})
		this.queryItems();
	},
	onReachBottom(){
		this.queryItems();
	},
	methods: {
		px(num) {
			return uni.upx2px(num) + "px"
		},
		btnDropChange: function(e) {
			let index = e.currentTarget.dataset.index;
			let arr = JSON.parse(JSON.stringify(this.attrArr[index].list));
			if (arr.length === 0) {
				this.$set(this.attrArr[index], "isActive", !this.attrArr[index].isActive)
			} else {
				this.attrData = arr;
				this.attrIndex = index;
				this.dropScreenShow = true;
				this.$set(this.attrArr[index], "isActive", false);
				this.scrollTop = 1;
				this.$nextTick(() => {
					this.scrollTop = 0
				});
			}
		},
		btnSelected: function(e) {
			let index = e.currentTarget.dataset.index;
			this.$set(this.attrData[index], "selected", !this.attrData[index].selected)
			this.queryItems('tabChange');
		},
		reset() {
			let arr = this.attrData;
			for (let item of arr) {
				item.selected = false;
			}
			this.attrData = arr
		},
		btnCloseDrop() {
			this.scrollTop = 1;
			this.$nextTick(() => {
				this.scrollTop = 0
			});
			this.dropScreenShow = false;
			this.attrIndex = -1
		},
		btnSure: function() {
			let index = this.attrIndex;
			let arr = this.attrData;
			let active = false;
			let attrName = "";
			//这里只是为了展示选中效果,并非实际场景
			for (let item of arr) {
				if (item.selected) {
					active = true;
					attrName += attrName ? ";" + item.name : item.name
				}
			}
			let obj = this.attrArr[index];
			this.btnCloseDrop();
			this.$set(obj, "isActive", active);
			this.$set(obj, "selectedName", attrName);
		},
		showDropdownList: function() {
			this.selectH = 246;
			this.tabIndex = 0;
		},
		hideDropdownList: function() {
			this.selectH = 0
		},
		dropdownItem: function(e) {
			let index = e.currentTarget.dataset.index;
			let arr = this.dropdownList;
			for (let i = 0; i < arr.length; i++) {
				if (i === index) {
					arr[i].selected = true;
				} else {
					arr[i].selected = false;
				}
			}
			this.dropdownList = arr;
			this.selectedName = index == 0 ? '综合' : '价格';
			this.selectH = 0
			
			this.queryItems('tabChange');
		},
		screen: function(e) {
			let index = e.currentTarget.dataset.index;
			if (index == 0) {
				this.showDropdownList();
			} else if (index == 1) {
				this.tabIndex = 1
				this.queryItems('tabChange');
			} else if (index == 2) {
				this.isList = !this.isList
			} else if (index == 3) {
				this.drawer = true
			}
		},
		closeDrawer: function() {
			this.drawer = false;
			this.queryItems('tabChange');
		},
		clearDrawer: function(){
			this.page.price1 = '',
			this.page.price2 = '',
			this.drawer = false;
		},
		back: function() {
			if (this.drawer) {
				this.closeDrawer()
			} else {
				// uni.navigateBack()
				uni.redirectTo({
					url:'/pages/search/search'
				})
			}
		},
		search: function() {
			uni.navigateTo({
				url: '/pages/search/search'
			})
		},
		detail: function() {
			uni.navigateTo({
				url: '../productDetail/productDetail'
			})
		},
		queryItems(source){
			if(source=='tabChange'){
				this.items = [];
				this.page.ipage  = 0;
			}
			
			this.loadding = true;
			
			let sort = '';
			if(this.tabIndex==1){
				sort = 'volume';
			}
			else {
				console.log(this.dropdownList);
				if(this.selectedName=='价格'){
					sort = 'coupon_price';
				}
				else if(this.selectedName=='综合'){
					sort = '';
				}
			}
			
			this.$http.post('/cms/goods/search', {ipage:this.page.ipage,q:this.searchKey,sort:sort,start_price:this.page.price1,end_price:this.page.price2}).then(res => {
				this.loadingType = 'noMore';
				if(res.data.items&&res.data.items){
					if(source=='tabChange'){
						this.items = res.data.items;
					}else{
						this.items.push(
							...res.data.items
						);
					}
					if(res.data.pager&&res.data.pager.ipage){
						this.page.ipage = parseInt(res.data.pager.ipage)+1;
					}
					if(res.data.items.length>=20){							
						this.loadingType = 'more';
					}
				}
				this.loadding = false;
				uni.stopPullDownRefresh()
			}).catch(err => {});
		},
	},
	onPullDownRefresh: function() {
		this.page.ipage = 1;
		this.pullUpOn = true;
		this.loadding = false
		this.queryItems('tabChange');
		
	},
	onReachBottom: function() {
		if (!this.pullUpOn) return;
		this.loadding = true;
		this.queryItems();
	},
}
